import React from "react"
import { graphql } from "gatsby"
import { Layout } from "../components/layout"
import SwiperCore, { Navigation, A11y } from "swiper"
import { Swiper, SwiperSlide } from "swiper/react"
import Img from "gatsby-image/withIEPolyfill"

import "swiper/swiper-bundle.css"

SwiperCore.use([Navigation, A11y])

export default function Template({ data }) {
  const { markdownRemark } = data // data.markdownRemark holds your post data
  const { frontmatter, html } = markdownRemark
  return (
    <Layout title={frontmatter.title} datePublished={frontmatter.isoDate}>
      <article className="post">
        <div className="post-header">
          <h1 className="post-title">{frontmatter.title}</h1>
          <p className="post-date">{frontmatter.date}</p>
        </div>

        <Swiper
          spaceBetween={15}
          slidesPerView={1}
          height={200}
          breakpoints={{
            768: {
              slidesPerView: 2,
            },
            1024: {
              slidesPerView: 3,
            },
          }}
          navigation
        >
          {frontmatter.images.map((image, index) => (
            <SwiperSlide key={index}>
              <Img
                objectFit="cover"
                className="post-image"
                fluid={image.file.thumbnail.fluid}
                alt={image.alt}
              />
            </SwiperSlide>
          ))}
        </Swiper>

        <div
          className="post-content"
          dangerouslySetInnerHTML={{ __html: html }}
        />
      </article>
    </Layout>
  )
}
export const pageQuery = graphql`
  query($path: String!) {
    markdownRemark(frontmatter: { path: { eq: $path } }) {
      html
      frontmatter {
        isoDate: date(formatString: "YYYY-MM-DDTHH:mm:ss.sssZ")
        date(formatString: "DD.MM.YYYY")
        path
        title
        images {
          alt
          file {
            thumbnail: childImageSharp {
              fluid(maxHeight: 200, maxWidth: 200, cropFocus: CENTER) {
                ...GatsbyImageSharpFluid_withWebp
              }
            }
          }
        }
      }
    }
  }
`
