import React, {useState} from "react"
import Helmet from "react-helmet"
import { ReactSEOMetaTags } from "react-seo-meta-tags"

import FontFaceObserver from 'fontfaceobserver';


export function Layout({ children, title, datePublished, blogPost }) {
    const [isFontLoaded, setIsFontLoaded] = useState(false);
    new FontFaceObserver('Jost').load().then(x => setIsFontLoaded(true), e => console.error("Failed to load font", e));

  return (
    <React.Fragment>
      <ReactSEOMetaTags
        render={el => (
          <Helmet defer={false} title={title}>
            <html lang="ru" className={isFontLoaded ? "fontLoaded" : ""}/>
            {el}
          </Helmet>
        )}
        website={{
          url: "https://yastova.ru", // The URL of this page (eg https://google.com/about)
          title, // Maximum 70 characters.
          // The original publication date. ISO 8601 timestamp eg "2019-10-06T13:56:03.123Z"
          // Don't know how useful for random webpages. Add it at least for blog posts.
          datePublished,
          description: "Description", // Maximum 200 characters.
          language: "ru", // Default "en-US" https://en.wikipedia.org/wiki/IETF_language_tag and https://datahub.io/core/language-codes
          image: "", // URL to the image, PNG, JPEG, or GIF recommended.
          // Possibly redundant property. But at least bots can scrape your email and that's fun right? :)
          author: {
            email: "liza@yastova.ru",
            name: " ",
            image:
              "http://www.yastova.ru/wp-content/themes/twentyten/images/elizaveta_yastova_002.jpg",
          },
        }}
        organization={{
          description: "description",
          name: "   ", // Eg "Google"
          legalName: "  ..", // Eg "Google Inc"
          logo:
            "http://www.yastova.ru/wp-content/themes/twentyten/images/logo.jpg", // URL to the logo image.
          // List of other webpages referencing this organization, eg Wikipedia, Facebook, Twitter, Instagram etc.
          // Will show these sites alongside your website in Google search results and probably boost your SEO rank too.
          url: "https://yastova.ru", // URL to the organization
        }}
      />
      <header className="header">
        <div className="header-content">
          <p> </p>
          <p> </p>
        </div>
      </header>

      <main role="main" className="content">
        {children}
      </main>
      <footer className="footer">
        <div className="footer-content">
          <a href="#"> </a>
          <a href="#"> </a>
          <span>    , 2020</span>
        </div>
      </footer>
    </React.Fragment>
  )
}
